import React, { Component } from 'react';

class Item extends Component {
    render(){
        return (
            <li className = "each-car">
                <span className = "car-title">{this.props.car.model}</span>
                <img  className = "car-image" src = {require(`${this.props.car.image}`)} 
                alt = {this.props.car.model}></img> 
                
                <section>
                <p><a  className = "car-info" href = {this.props.car.spec} >More info?</a></p>
                <span className = "car-price"> {`Price: $${this.props.car.price}`} </span>
                <button className = "car-buy" onClick = {this.props.add}>Add Cart</button>
                </section>
            </li>
        )
    }
}

export default Item;